<template>
	<view>
		<div>背景音开关</div>
		<u-switch v-model="myAudio" @change="changeBgcState"></u-switch>
	</view>
</template>

<script>
import { mapGetters, mapMutations, mapState } from 'vuex';
const App = getApp();
export default {
	//获取小程序实例
	computed: {
		...mapState(['playAudio'])
	},
	data() {
		return {
			myAudio: true
		};
	},
	methods: {
		//查看修改音频状态
		...mapMutations(['updatePlayAudio']),
		changeBgcState() {
			//自动改变状态  只要状态改变 就存储更新
			console.log(this.myAudio);

			// //如果开启了 直接开启声音且 app下次开启也有声音
			// //如果关闭了 直接停止声音且 app下次开启也没声音
			uni.setStorageSync('playAudio', this.myAudio);
			this.updatePlayAudio();
			//先操作播放与否再存储本地
			App.changeAudioState(this.myAudio);
		}
	}
};
</script>

